/**
 * External dependencies
 */
import { useState } from '@wordpress/element';

import ClickableList from '../../clickable-list';
import { DataTabProvider, useDataTab } from './context';
import {
	Flex,
	Button,
	PanelBody,
	TextareaControl,
	Icon,
	SearchControl,
} from '@wordpress/components';
import { external, arrowLeft } from '@wordpress/icons';
import ReactJson from 'react-json-view';
import './style.scss';

/**
 * Internal dependencies
 */
import StoreView from './store-view';

const MySearchControl = ( { value, onChange } ) => (
	<SearchControl
		className="gutenbuild-searchcontrol"
		label={ 'Search' }
		hideLabelFromVision={ true }
		value={ value }
		onChange={ ( val ) => onChange( val ) }
	/>
);

const getDocLink = ( module ) => {
	return `https://developer.wordpress.org/block-editor/reference-guides/data/data-core${ module }`;
};

const getDocModule = ( activeStore ) => {
	const s = activeStore.split( '/' );

	// Not Core
	if ( s[ 1 ] ) {
		return '-' + s[ 1 ];
	}

	return '';
};

const ViewHeader = ( { title, link, onBack } ) => {
	return (
		<Flex
			className="gutenbuild-view-header"
			align="center"
			justify="flex-start"
		>
			<div style={ { paddingRight: '8px', height: '24px' } }>
				<a href="#" onClick={ onBack }>
					<Icon icon={ arrowLeft } />
				</a>
			</div>
			<Flex align="center" justify="flex-start">
				<a href={ link } target="_blank">
					{ title }
				</a>
				<Icon size={ 18 } icon={ external } />
			</Flex>
		</Flex>
	);
};



const ActiveStoreView = () => {
	const {
		action,
		setAction,
		activeStore,
		setActiveStore,
		setActiveFunction,
	} = useDataTab();
	const [ keyword, setKeyword ] = useState( '' );

	const mappedFunctions = Object.keys( wp.data[ action ]( activeStore ) )
		.map( ( i ) => {
			return {
				text: `${ i }()`,
				value: i,
			};
		} )
		.sort( alphaSort );

	const docLink = getDocLink( getDocModule( activeStore ) );

	return (
		<div>
			<ViewHeader
				title={ activeStore }
				link={ docLink }
				onBack={ () => setActiveStore( null ) }
			/>
			<PanelBody>
				<MySearchControl
					value={ keyword }
					onChange={ ( val ) => setKeyword( val ) }
				/>
			</PanelBody>

			<Flex style={ { padding: '1rem' } }>
				<span>
					<input
						type="radio"
						id="selector"
						name="action"
						value="select"
						checked={ action === 'select' }
						onChange={ ( i ) => setAction( 'select' ) }
					/>
					<label htmlFor="selector">Selectors</label>
				</span>
				<span>
					<input
						type="radio"
						id="actions"
						name="action"
						value="dispatch"
						onChange={ ( i ) => setAction( 'dispatch' ) }
						checked={ action === 'dispatch' }
					/>
					<label htmlFor="actions">Actions</label>
				</span>
			</Flex>

			<ClickableList
				items={ mappedFunctions.filter( ( i ) =>
					i.text.toLowerCase().includes( keyword.toLowerCase() )
				) }
				onClick={ ( i ) => setActiveFunction( i.value ) }
			/>
		</div>
	);
};

const ActiveFunctionView = () => {
	const {
		action,
		activeStore,
		activeFunction,
		setActiveFunction,
	} = useDataTab();
	const [ output, setOutput ] = useState();
	const [ isOutputObject, setOutputObject ] = useState();

	const callFunction = async () => {
		try {
			let out = await wp.data[ action ]( activeStore )[
				activeFunction
			]();

			if ( out === undefined ) {
				out = 'undefined';
			}

			setOutput( out );
			setOutputObject( _.isObject( out ) );
		} catch ( e ) {
			setOutput(
				"Error running function. Most likely missing arguments that currently isn't supported."
			);
		}
	};

	const docLink = `${ getDocLink(
		getDocModule( activeStore )
	) }/#${ activeFunction.toLowerCase() }`;

	return (
		<div>
			<ViewHeader
				title={ activeFunction }
				link={ docLink }
				onBack={ () => setActiveFunction( null ) }
			/>
			<PanelBody>
				<Button variant="primary" onClick={ callFunction }>
					Run Function
				</Button>
			</PanelBody>
			<PanelBody title="Output">
				{ isOutputObject ? (
					<ReactJson src={ output } />
				) : (
					<TextareaControl value={ JSON.stringify( output ) } />
				) }
			</PanelBody>
		</div>
	);
};

const DataTab = () => {
	const { activeStore, activeFunction } = useDataTab();

	if ( activeStore && ! activeFunction ) {
		return <ActiveStoreView />;
	}

	if ( activeFunction ) {
		return <ActiveFunctionView />;
	}

	return <StoreView />;
};

export default ( props ) => (
	<DataTabProvider>
		<DataTab { ...props } />
	</DataTabProvider>
);
